import Arrow from '../image/icon/arrow(white).png';
import Round from '../image/icon/scroll-round.png';
import { Link } from 'react-scroll';
export default function Header(){

  const show = () => { //logo 보여지기
    const elLogo = document.querySelector('.logo');
    elLogo.classList.add('active');
  };
  const hide = () => { //logo 숨기기
    const elLogo = document.querySelector('.logo');
    elLogo.classList.remove('active');
  };




  return(
    <header>
        <div className="head">
          <a href="#main">
          <h2 className="logo" onMouseOver={show} onMouseOut={hide}>
            <span className="show">D</span>
            <span className="hide">ongwoo's </span>
            <span className="show"> P</span>
            <span className="hide">ortfolio</span>
            <span className="show">`</span>
          </h2>
          </a>
          <nav className="menu">
            <a href="#about">About</a>
            <a href="#pro">Project</a>
            <a href="#con">Contact</a>
          </nav>
        </div>
        <div className="arrow">
          <div className="arr">
            <img src={Arrow} alt="round"/>
            <div className="round">
              <img src={Round} alt="arrow"/>  
            </div>
          </div>
          
        </div>
      </header>
  );
};